.somos-container {
  background: var(--anaranjadoDegradado);
  height: 200vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 20px;
}
.somos {
  max-width: 1200px;
  width: 100%;
  box-sizing: border-box;
  position: relative;
}

.somos-container h2 {
  font-size: 40px;
  text-align: center;
  margin-bottom: 50px;
  color: #fff;
}
.somos .linea {
  position: absolute;
  width: 5px;
  height: 85%;
  background-color: #646464;
  border-radius: 5px;
  z-index: 600;
  margin-top: 45px;
  left: 50%;
  top: 25px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
