* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}
:root {
  --default-animation: all 0.3s ease-in-out;
  --primary-color: #fc4d19;
  --soft-black: #1c1b1e;
  --anaranjado: #ff815b;
  --anaranjadoClaro: #ffc8b8;
  --rojo: #de6262;
  --negro: #373737;
  --negroClaro: #565656;
  --anaranjadoDegradado: linear-gradient(
    180deg,
    rgba(255, 184, 140, 1) 0%,
    rgba(222, 98, 98, 1) 100%
  );
  --gris: #e8e8e8;
}

.swiper-button-prev,
.swiper-button-next {
  color: white !important;
  top: 85% !important;
  width: 30px !important;
  height: 30px !important;
  background-color: #fc4d19;
  background-color: var(--primary-color);
  border-radius: 50%;
  padding: 20px;
  transition: all 0.3s ease-in-out;
  transition: var(--default-animation);
  position: fixed !important;
  z-index: 200 !important;
}
.swiper-button-prev {
  left: 5% !important;
}
.swiper-button-next {
  right: 5% !important;
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.swiper-button-prev:active,
.swiper-button-next:active {
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 35px !important;
  font-weight: bold;
}

.swiper-wrapper {
  transition-duration: 300ms !important;
}
.arrow-down-container {
  position: fixed;
  bottom: 50px;
  left: calc(50% - 40px);
  z-index: 10;
  border-radius: 50%;
  background-color: #fc4d19;
  background-color: var(--primary-color);
  transition: all 0.3s ease-in-out;
  transition: var(--default-animation);
  width: 80px;
  height: 80px;
  border: none;
  -webkit-animation: arrow-down 1s infinite;
          animation: arrow-down 1s infinite;
}
.arrow-down {
  font-size: 50px;
  color: white;
}
@-webkit-keyframes arrow-down {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes arrow-down {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.navigation-buttons {
  position: fixed;
  z-index: 10;
  top: 10px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}
.container-buttons-slide {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  position: relative;
}
.container-buttons-slide > *::after {
  content: "";
